<template>
  <header class="header">
    <nav class="header-navbar" v-show="header">
      <button
        class="header-navbar__lang"
        v-if="lang == 'ru'"
        @click="changeLang"
      >
        {{ words.header.lang[lang] }}
      </button>
      <button class="header-navbar__lang" v-else @click="changeLang">
        {{ words.header.lang[lang] }}
      </button>
      <h2 class="header-navbar__title">{{ words.header.title[lang] }}</h2>
      <button class="header-navbar__search" @click="$emit('header', false)">
        <i class="fal fa-search"></i>
      </button>
    </nav>
    <form class="header-search" @submit.prevent v-show="!header">
      <button class="header-search__back" @click="$emit('header', true)">
        <i class="fal fa-arrow-left"></i>
      </button>
      <input
        type="text"
        :placeholder="words.header.placeholder[lang]"
        class="header-search__input"
        v-model="search"
      />
      <button class="header-search__clear" @click="search = ''">
        <i class="fal fa-times"></i>
      </button>
    </form>
  </header>
</template>

<script>
export default {
  name: "Navbar",
  props: {
    lang: String,
    header: Boolean,
  },
  data() {
    return {
      search: "",
      words: null,
    };
  },
  created() {
    this.words = JSON.parse(localStorage.words);
  },
  methods: {
    changeLang() {
      this.$emit("changeLang", this.lang == "ru" ? "uz" : "ru");
    },
  },
  watch: {
    search(val) {
      this.$emit("search", val);
    },
  },
};
</script>
