<template>
  <div class="modal">
    <div class="modal-block">
      <h3 class="modal-block__title">
        {{ edit ? "Eslatmani Tahrirlash" : "Eslatma qo’shish" }}
      </h3>
      <div class="modal-block__inputs">
        <label>
          <span>Sarlovha</span>
          <input type="text" placeholder="Sarlovha..." v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <textarea placeholder="Content..." v-model="descr"></textarea>
        </label>
      </div>
      <div class="modal-block__btns">
        <button class="modal-block__btn modal-block__close" @click="closeModal">
          Bekor Qilish
        </button>
        <button
          class="modal-block__btn modal-block__add"
          @click="addNote"
          v-if="!edit"
        >
          Qo'shish
        </button>
        <button
          class="modal-block__btn modal-block__edit"
          @click="editNote"
          v-else
        >
          O'zgartirish
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
export default {
  name: "Modal",
  props: {
    edit: Boolean,
    editNote: Object,
  },
  data() {
    return {
      title: "",
      descr: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
    },
    addNote() {
      if (this.title != "" && this.descr != "") {
        const item = {
          id: uuidv4(),
          title: this.title,
          descr: this.descr,
          data: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", item);
        this.closeModal();
      } else {
        alert("Sarlovha va Contentni Toldiring");
      }
    },
    editNote() {
      if (this.title != "" && this.descr != "") {
        const item = {
          id: this.editNote.id,
          title: this.title,
          descr: this.descr,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editedNote", item);
        this.closeModal();
      } else {
        alert("Sarlovha va Contentni Toldiring");
      }
    },
  },
  mounted() {
    if (this.edit) {
      this.title = this.editNote.title;
      this.descr = this.editNote.descr;
    } else {
      return;
    }
  },
};
</script>
