<template>
  <div class="notes-item">
    <div class="notes-item__top" :class="{ between: !grid }">
      <h3 class="notes-item__title">{{ note.title }}</h3>
      <p class="notes-item__date">{{ note.date }}</p>
    </div>
    <p class="notes-item__descr">{{ note.descr }}</p>
    <div class="notes-item__btns">
      <button class="notes-item__btn notes-item__btn-edit" @click="$emit('changeNote', note.id)">
        <i class="fal fa-pencil"></i>
        <span>O'zgartirish</span>
      </button>
      <button class="notes-item__btn notes-item__btn-delete" @click="$emit('delNote', note.id)">
        <i class="fas fa-trash-alt"></i>
        <span>O'chirish</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    grid: Boolean,
    note: Object,
  },
};
</script>
